* {
    box-sizing: border-box;
}
  
  html,
  body {
    background: #000 url('img/background.jpg') no-repeat center center/cover;
    
  }
  
  

.grid-container {
    width: 70%;
    display: grid;
    margin: 0 auto;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    grid-gap: 10px;
}

.btn {
    display: flex;
    justify-content: center;
    margin: 20px 0;
}

.btn button {
    border-style: none;
    padding: 5px 10px;
    margin-right: 10px;
    border-radius: 5px;
    background: #30a7d7;
    color: #fff;
    font-size: 1em;
}
  
  header {
    height: 300px;
  }
  
  header img {
    width: 600px;
  }
  
  .center {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  